import React, { Component } from "react";
import DeleteBtn from "../../components/DeleteBtn";
import Jumbotron from "../../components/Jumbotron";
import API from "../../utils/API";
import { Link } from "react-router-dom";
import { Col, Row, Container } from "../../components/Grid";
import { List, ListItem } from "../../components/List";
import { Input, TextArea, FormBtn } from "../../components/Form";

class Games extends Component {
  state = {
    games: [],
    sport: "",
    location: "",
    description: ""
  };

  componentDidMount() {
    this.loadGames();
  }

  loadGames = () => {
    API.getGames()
      .then(res =>
        this.setState({ games: res.data, sport: "", location: "", description: "" })
      )
      .catch(err => console.log(err));
  };

  deleteGame = id => {
    API.deleteGame(id)
      .then(res => this.loadGames())
      .catch(err => console.log(err));
  };

  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handleFormSubmit = event => {
    event.preventDefault();
    if (this.state.sport && this.state.location) {
      API.saveGame({
        sport: this.state.sport,
        location: this.state.location,
        description: this.state.description
      })
        .then(res => this.loadGames())
        .catch(err => console.log(err));
    }
  };

  render() {
    return (
      <Container fluid>
        <Row>
          <Col size="md-6">
            <Jumbotron>
              <h1>Create Game Here!</h1>
            </Jumbotron>
            <form>
              <Input
                value={this.state.sport}
                onChange={this.handleInputChange}
                name="sport"
                placeholder="Sport (required)"
              />
              <Input
                value={this.state.location}
                onChange={this.handleInputChange}
                name="location"
                placeholder="Location (required)"
              />
              <TextArea
                value={this.state.description}
                onChange={this.handleInputChange}
                name="description"
                placeholder="Description (Optional)"
              />
              <FormBtn
                disabled={!(this.state.location && this.state.sport)}
                onClick={this.handleFormSubmit}
              >
                Submit Book
              </FormBtn>
            </form>
          </Col>
          <Col size="md-6 sm-12">
            <Jumbotron>
              <h1>Games On My List</h1>
            </Jumbotron>
            {this.state.games.length ? (
              <List>
                {this.state.games.map(games => (
                  <ListItem key={games._id}>
                    <Link to={"/games/" + games._id}>
                      <strong>
                        {games.sport} by {games.location}
                      </strong>
                    </Link>
                    <DeleteBtn onClick={() => this.deleteGame(games._id)} />
                  </ListItem>
                ))}
              </List>
            ) : (
              <h3>No Results to Display</h3>
            )}
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Games;



